import { useEffect } from "react";
import { Outlet, useParams } from "react-router-dom";
import "../styles/Order.scss";
import { gsap } from "gsap";
import { ScrollTrigger, SplitText } from "gsap/all";

export default function Order() {
  gsap.registerPlugin(ScrollTrigger);
  let { menuId } = useParams();

  useEffect(() => {
    let footer = document.querySelector("footer");
    let logos = document.querySelectorAll(".footer__logo");
    let spans = document.querySelectorAll(".footer__text span");
    var h = footer.offsetHeight;

    let texts = new SplitText(spans, { type: "chars" });

    const footerLogo = () => {
      var w = window.innerWidth;

      logos.forEach((logo) => {
        gsap.set(logo, {
          x: `random(0, ${w})`,
        });
      });
    };
    let tl = gsap.timeline({
      repeat: -1,
      repeatDelay: -1,
      scrollTrigger: {
        trigger: footer,
        start: "-20% center",
        toggleActions: "restart none none none",
      },
    });

    let textTl = gsap.timeline({
      scrollTrigger: {
        trigger: footer,
        start: "-20% center",
        toggleActions: "restart none none none",
      },
    });

    textTl.fromTo(texts.chars, { scale: 0 }, { scale: 1, duration: 1 });
    tl.fromTo(
      logos,
      {
        y: -50,
      },
      { y: h + 200, duration: 4, stagger: 0.1, rotate: "random(-360,360)" }
    );
    footerLogo();

    return () => {
      tl.scrollTrigger.kill(true);
      tl.pause().kill(true);
    };
  });

  useEffect(() => {
    let texts = new SplitText(".almighty h1", { type: "chars" });
    texts.chars.forEach((text) => {
      text.addEventListener("mouseenter", () => {
        gsap.to(text, { rotateY: "180deg", color: "red" });
      });
      text.addEventListener("mouseleave", () => {
        gsap.to(text, { rotateY: "0", color: "black" });
      });
    });
  });

  return (
    <>
      <div className="order">
        <div className="almighty">
          <h1>almighty taco</h1>
        </div>
        <div className="menu">
          <span className="menu__text">{menuId}</span>
          <span></span>
        </div>
      </div>
      <Outlet />
      <footer>
        <div className="footer__logo"></div>
        <div className="footer__logo"></div>
        <div className="footer__logo"></div>
        <div className="footer__logo"></div>
        <div className="footer__logo"></div>
        <div className="footer__logo"></div>
        <div className="footer__logo"></div>
        <div className="footer__logo"></div>
        <div className="footer__logo"></div>
        <div className="footer__logo"></div>
        <div className="footer__logo"></div>
        <div className="footer__logo"></div>
        <div className="footer__logo"></div>
        <div className="footer__logo"></div>
        <div className="footer__logo"></div>
        <div className="footer__logo"></div>
        <div className="footer__text">
          <span>enjoy</span>
          <span> almighty taco!</span>
        </div>
      </footer>
    </>
  );
}
