import SetQty from "./SetQty";
import SetToppings from "./SetToppings";
import { Context } from "../Context";
import { useState, useContext } from "react";
import { translateAlert } from "../helper";

export default function SetOrder({ food, storeId, menu }) {
  const [topping, setTopping] = useState([]);
  const [qty, setQty] = useState(0);
  const { updateItem } = useContext(Context);

  const handleCheck = (e) => {
    var updatedList = [...topping];
    if (e.target.checked) {
      updatedList = [...topping, e.target.value];
    } else {
      updatedList.splice(topping.indexOf(e.target.value), 1);
    }
    setTopping(updatedList);
  };

  let inputs = document.querySelector(".toppings");

  const Order = (e) => {
    let input = inputs.querySelectorAll("input[type=checkbox]");
    if (qty > 0) {
      updateItem(food, qty, topping);
      setQty(0);
      setTopping([]);
      input.forEach((el) => (el.checked = false));
    } else {
      translateAlert(storeId);
      input.remove();
    }
  };

  return (
    <form id="form">
      <h6
      className={menu ? "prevent" : ""}
      >
        add toppings
      </h6>
      <SetToppings
        storeId={storeId}
        handleCheck={handleCheck}
        foodName={food.name}
        menu={menu}
      />
      <SetQty qty={qty} setQty={setQty} />
      <button type="reset" onClick={Order}>
        {storeId === "korea" ? "" : "ADD TO ORDER"}
      </button>
    </form>
  );
}
