import React, { useEffect, useRef, useState } from "react";
import { useNavigate } from "react-router-dom";
import "../styles/Store.scss";
import Select from "react-select";
import { gsap } from "gsap";
import Loading from "../components/Loading";

export default function Store() {
  const [store, setStore] = useState("");
  const selectRef = useRef();
  const navigate = useNavigate();
  const [load, setLoad] = useState(false);

  const goToStore = () => {
    localStorage.setItem("store", store);
    store === "" ? selectRef.current.focus() : navigate(`${store}/main`);
  };
  const handleChange = (event) => {
    setStore(event.value);
    gsap.to(".storeBtn", { scale: 1.2, ease: "Expo.easeInOut" });
  };
  const stores = [
    { value: "korea", label: "Korea" },
    { value: "mexico", label: "Mexico" },
    { value: "usa", label: "Usa" },
  ];

  const customStyles = {
    container: (provided) => ({
      ...provided,
      borderRadius: "50px",
      border: "1px solid black",
      background: "#fff",
    }),
    option: (provided) => ({
      ...provided,
    }),
    control: (provided) => ({
      ...provided,
      borderRadius: "50px",
    }),
    placeholder: (provided) => ({
      ...provided,
      textTransform: "uppercase",
      color: "black",
      textAlign: "center",
    }),
    singleValue: (provided) => ({
      ...provided,
      textTransform: "uppercase",
      textAlign: "center",
    }),
    menu: (provided) => ({
      ...provided,
      margin: 0,
      textAlign: "center",
      textTransform: "uppercase",
    }),
  };
  useEffect(() => {
    gsap.set(".store__imgs div", { autoAlpha: 0 });

    gsap
      .timeline({ defaults: { duration: 1 } })
      .to(".store__imgs div", { autoAlpha: 1, stagger: 0.5 })
      .to(".store__imgs div img", { autoAlpha: 1, stagger: 0.5 });
  }, []);

  return (
    <div className="store">
      {load ? (
        <Loading />
      ) : (
        <>
          <div className="almighty">
            <h3>enjoy</h3>
            <h1>almighty taco</h1>
          </div>
          <div className="store__imgs">
            <div>
              <img
                src={`${process.env.PUBLIC_URL}/images/taco.png`}
                alt="store__imgs1"
              />
            </div>
            <div>
              <img
                src={`${process.env.PUBLIC_URL}/images/breakfast-burrito.png`}
                alt="store__imgs2"
              />
            </div>
          </div>
          <div className="selectForm">
            <Select
              styles={customStyles}
              className="select"
              placeholder="Please pick store to Order"
              options={stores}
              ref={selectRef}
              onChange={handleChange}
            />
            <button
              className="storeBtn"
              onClick={goToStore}
              disabled={store ? false : true}
            >
              Go grab taco
            </button>
          </div>
        </>
      )}
    </div>
  );
}
