import { useEffect } from "react";
import { translateToppings } from "../helper";

export default function SetToppings({ storeId, handleCheck, foodName, menu }) {
  const toppings = translateToppings(storeId);

  useEffect(() => {
    const slider = document.querySelectorAll(".toppings");

    slider.forEach((slide) => {
      let isDown = false;
      let startX;
      let scrollLeft;
      slide.addEventListener("mousedown", (e) => {
        isDown = true;
        slide.classList.add("active");
        startX = e.pageX - slide.offsetLeft;
        scrollLeft = slide.scrollLeft;
      });

      slide.addEventListener("mouseleave", () => {
        isDown = false;
        slide.classList.remove("active");
      });

      slide.addEventListener("mouseup", () => {
        isDown = false;
        slide.classList.remove("active");
      });

      slide.addEventListener("mousemove", (e) => {
        if (!isDown) return;
        e.preventDefault();
        const x = e.pageX - slide.offsetLeft;
        const walk = (x - startX) * 1;
        slide.scrollLeft = scrollLeft - walk;
      });
    });
  });

  return (
    <div
      className={menu ? "toppings prevent" : "toppings"}
    >
      {toppings.map(({ value, image }, i) => (
        <div key={i}>
          <input
            value={value}
            id={foodName + i}
            type="checkbox"
            onChange={handleCheck}
          />
          <label
            htmlFor={foodName + i}
            style={{ backgroundImage: `url(${image})` }}
          >
            {value}
          </label>
        </div>
      ))}
    </div>
  );
}
