export default function SetQty({ qty, setQty }) {
  return (
    <>
      <div className="quantityBox">
        <span
          id="minus"
          onClick={() => {
            qty <= 0 ? setQty(0) : setQty(qty - 1);
          }}
        >
          &#8722;
        </span>
        <span id="qty" className={qty >= 1 ? "qty" : ""}>
          {qty}
        </span>
        <span
          id="plus"
          onClick={() => {
            setQty(qty + 1);
          }}
        >
          &#43;
        </span>
      </div>
    </>
  );
}
