import "../styles/Nav.scss";
import { gsap } from "gsap";
import { useNavigate } from "react-router-dom";
import { useEffect, useState, useContext, useRef } from "react";
import { Context } from "../Context";
import Cart from "./Cart";

export default function Nav() {
  const animation = useRef();
  const { order } = useContext(Context);
  const navigate = useNavigate();
  const [store, setStore] = useState("");

  useEffect(() => {
    setStore(localStorage.getItem("store"));
  }, [setStore]);
  const setMenuURL = (menu) => {
    animation.current.reversed(!animation.current.reversed()).resume();
    navigate(`${store}/${menu}`);
  };

  useEffect(() => {
    animation.current = gsap.timeline({ paused: true, reversed: true });
    animation.current
      .to(".nav__close", { opacity: 0 })
      .set(".nav__open", { display: "flex" })
      .to(".nav__open", { opacity: 1 });
  });

  const navClick = () => {
    animation.current.reversed(!animation.current.reversed()).resume();
  };


  const cartopen = () => {
    gsap.timeline().to(".cart", { right: 0 }).to(".cartbg", { opacity: 1 });
  };

  return (
    <nav
      style={
        window.location.pathname === "/almightyTaco/"
          ? { opacity: 0 }
          : { opacity: 1 }
      }
    >
      <div className="navWrap">
        <span className="nav__close" onClick={navClick}>
          &#9776;
        </span>
        <div className="nav__open">
          <span onClick={navClick}>&#9747;</span>
          <div
            onClick={() => {
              setMenuURL("main");
            }}
          >
            {store === "korea" ? "" : "Main menu"}
          </div>
          <div
            onClick={() => {
              setMenuURL("beverages");
            }}
          >
            {store === "korea" ? "" : "beverages"}
          </div>
        </div>

        <span className="cartbtn" onClick={cartopen}>
          cart
          <b>
            ({order.length >= 10 ? "" : 0}
            {order.length})
          </b>
        </span>
        <Cart location={store} />
      </div>
    </nav>
  );
}
