import React, { useContext } from "react";
import { gsap } from "gsap";
import { Context } from "../Context";
import { totalPrice } from "../helper";
import "../styles/Cart.scss";
export default function Cart({ location }) {
  const { order, deleteItem } = useContext(Context);

  const cartclose = () => {
    gsap.timeline().to(".cartbg", { opacity: 0 }).to(".cart", { right: "-100vw" });
  };
  return (
    <div className="cart">
      <div className="cartbg" onClick={cartclose}></div>
      <div className="cart__wrap">
        <h1>
          my order <span onClick={cartclose}>&#10140;</span>
        </h1>
        {order.length === 0 ? (
          <p className="orderYet">have you ordered taco yet?</p>
        ) : (
          <>
            <div className="reciptWrap">
              {order &&
                order.map((item, index) => (
                  <div className="myFood" key={index}>
                    <span className="index">{index + 1}</span>
                    <div>
                      <h2>
                        {location === "korea"
                          ? item.food.translate
                          : item.food.name}
                      </h2>
                      <p className="topping">
                        {location === "korea" ? " :" : "toppings :"}
                        {item.topping === undefined ||
                        item.topping.length === 0 ? (
                          <span> none</span>
                        ) : (
                          item.topping.map((t, index) => (
                            <span key={index}> {t},</span>
                          ))
                        )}
                      </p>
                    </div>
                    <span className="qty">{item.qty}</span>
                    <span className="price">
                      {location === "korea"
                        ? item.price + ""
                        : "$" + item.price}
                    </span>
                    <span
                      className="delete"
                      onClick={() => {
                        deleteItem(index);
                      }}
                    >
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        fill="none"
                        viewBox="0 0 24 24"
                        stroke="red"
                        strokeWidth="2"
                      >
                        <path
                          strokeLinecap="round"
                          strokeLinejoin="round"
                          d="M15 12H9m12 0a9 9 0 11-18 0 9 9 0 0118 0z"
                        />
                      </svg>
                    </span>
                  </div>
                ))}
            </div>
            <div className="total">
              {location === "korea" ? " : " : "TOTAL PRICE : $"}
              {order && 0 + totalPrice(Array.from(order, ({ price }) => price))}
              {location === "korea" ? "" : ""}
            </div>
            <button>{location === "korea" ? "" : "order now"}</button>
          </>
        )}
      </div>
    </div>
  );
}
