import "../styles/Loading.scss";
import { gsap } from "gsap/all";
import { useEffect } from "react";

export default function Loading() {
  useEffect(() => {
    gsap
      .timeline({
        repeat: -1,
      })
      .add("start")
      .to(
        ".loadingImg1",
        {
          y: -50,
          rotate: 360,
        },
        "start"
      )
      .to(
        ".loadingImg1",
        {
          y: 0,
        },
        "start+=0.2"
      )
      .to(
        ".loadingImg2",
        {
          y: -50,
          rotate: 360,
        },
        "start+=0.2"
      )
      .to(
        ".loadingImg2",
        {
          y: 0,
        },
        "start+=0.4"
      )
      .to(
        ".loadingImg3",
        {
          y: -50,
          rotate: 360,
        },
        "start+=0.4"
      )
      .to(
        ".loadingImg3",
        {
          y: 0,
        },
        "start+=0.6"
      );
  });
  return (
    <>
      <div className="loadingWrap">
        <img
          className="loadingImg1"
          src={`${process.env.PUBLIC_URL}/logo192.png`}
          alt="loading"
        />

        <img
          className="loadingImg2"
          src={`${process.env.PUBLIC_URL}/logo192.png`}
          alt="loading"
        />
        <img
          className="loadingImg3"
          src={`${process.env.PUBLIC_URL}/logo192.png`}
          alt="loading"
        />
      </div>
    </>
  );
}
