import "../styles/Food.scss";
import { useEffect } from "react";
import { gsap } from "gsap/all";
import { useParams } from "react-router-dom";
import SetOrder from "../components/SetOrder";
import { translateMain, translateBeverage } from "../helper";

export default function Food() {
  let { storeId, foodId } = useParams();
  const data = [
    ...translateMain(storeId),
    ...translateBeverage(storeId),
  ].filter(({ name }) => name === foodId);

  const beveragesName = translateBeverage(storeId)
    .map(({ name }) => name)
    .includes(foodId);

  useEffect(() => {
    const tl = gsap.timeline({ defaults: { duration: 1, ease: "power4" } });
    gsap.set(".food__pic img", { autoAlpha: 0 });
    tl.fromTo(".food__wrap", { autoAlpha: 0 }, { autoAlpha: 1 }).to(
      ".food__pic img",
      { autoAlpha: 1 },
      "<50%"
    );

    return () => {
      tl.pause().kill(true);
    };
  });
  return (
    <div className="food">
      {data.map((food, i) => (
        <div className="food__wrap" key={i}>
          <div className="food__pic">
            <img src={food.image} alt="foodpic" />
          </div>
          <div className="food__info">
            <div>
              <h1>{storeId === "korea" ? food.translate : food.name}</h1>
              <div className="price">
                {storeId === "korea" ? (
                  <span>{food.price}</span>
                ) : (
                  <span>${food.price}</span>
                )}
              </div>
            </div>
            <SetOrder storeId={storeId} food={food} menu={beveragesName} />
          </div>
        </div>
      ))}
    </div>
  );
}
