import { useEffect, useState } from "react";
import { gsap, Draggable, ScrollTrigger } from "gsap/all";
import { useParams, Link } from "react-router-dom";
import { translateMain, translateBeverage } from "../helper";

const color = [
  "#FEF0CA",
  "#FFD66C",
  "#7FD1AE",
  "#FF9330",
  "#FFD3AC",
  "#E0B8D6",
  "#E9DFC6",
  "#CFD5B2",
  "#A3E4D9",
  "#9F6B4E",
];

export default function Foods() {
  gsap.registerPlugin(Draggable, ScrollTrigger);
  let { storeId, menuId } = useParams();
  const [data, setData] = useState([]);

  useEffect(() => {
    if (!menuId) {
      return;
    }
    if (menuId === "main") {
      setData(translateMain(storeId));
    } else if (menuId === "beverages") {
      setData(translateBeverage(storeId));
    } else {
      return;
    }
  }, [setData, menuId, storeId]);

  function FoodList() {
    const callback = () => {
      gsap.utils.toArray("#menuSlide").map((line, i) => {
        const links = line.querySelectorAll(".foods"),
          tl = horizontalLoop(links, {
            repeat: -1,
            speed: 1 + i * 0.5,
            draggable: true,
            reversed: false,
            paddingRight: parseFloat(
              gsap.getProperty(links[0], "marginRight", "px")
            ),
          });
        links.forEach((link) => {
          link.addEventListener("mouseenter", () =>
            gsap.to(tl, { timeScale: 0, overwrite: true })
          );
          link.addEventListener("mouseleave", () =>
            gsap.to(tl, { timeScale: i ? -1 : 1, overwrite: true })
          );
        });
      });

      function horizontalLoop(items, config) {
        items = gsap.utils.toArray(items);
        config = config || {};
        let tl = gsap.timeline({
            repeat: config.repeat,
            defaults: { ease: "none" },
            onReverseComplete: () =>
              tl.totalTime(tl.rawTime() + tl.duration() * 100),
          }),
          length = items.length,
          startX = 0,
          times = [],
          widths = [],
          xPercents = [],
          curIndex = 0,
          pixelsPerSecond = (config.speed || 1) * 100,
          snap =
            config.snap === false
              ? (v) => v
              : gsap.utils.snap(config.snap || 1), // some browsers shift by a pixel to accommodate flex layouts, so for example if width is 20% the first element's width might be 242px, and the next 243px, alternating back and forth. So we snap to 5 percentage points to make things look more natural
          populateWidths = () =>
            items.forEach((el, i) => {
              widths[i] = parseFloat(gsap.getProperty(el, "width", "px"));
              xPercents[i] = snap(
                (parseFloat(gsap.getProperty(el, "x", "px")) / widths[i]) *
                  100 +
                  gsap.getProperty(el, "xPercent")
              );
            }),
          getTotalWidth = () =>
            items[length - 1]?.offsetLeft +
            (xPercents[length - 1] / 100) * widths[length - 1] -
            startX +
            items[length - 1].offsetWidth *
              gsap.getProperty(items[length - 1], "scaleX") +
            (parseFloat(config.paddingRight) || 0),
          totalWidth,
          curX,
          distanceToStart,
          distanceToLoop,
          item,
          i;
        populateWidths();
        gsap.set(items, {
          // convert "x" to "xPercent" to make things responsive, and populate the widths/xPercents Arrays to make lookups faster.
          xPercent: (i) => xPercents[i],
        });
        gsap.set(items, { x: 0 });
        totalWidth = getTotalWidth();
        for (i = 0; i < length; i++) {
          item = items[i];
          curX = (xPercents[i] / 100) * widths[i];
          distanceToStart = item?.offsetLeft + curX - startX;
          distanceToLoop =
            distanceToStart + widths[i] * gsap.getProperty(item, "scaleX");
          tl.to(
            item,
            {
              xPercent: snap(((curX - distanceToLoop) / widths[i]) * 100),
              duration: distanceToLoop / pixelsPerSecond,
            },
            0
          )
            .fromTo(
              item,
              {
                xPercent: snap(
                  ((curX - distanceToLoop + totalWidth) / widths[i]) * 100
                ),
              },
              {
                xPercent: xPercents[i],
                duration:
                  (curX - distanceToLoop + totalWidth - curX) / pixelsPerSecond,
                immediateRender: false,
              },
              distanceToLoop / pixelsPerSecond
            )
            .add("label" + i, distanceToStart / pixelsPerSecond);
          times[i] = distanceToStart / pixelsPerSecond;
        }
        function toIndex(index, vars) {
          vars = vars || {};
          Math.abs(index - curIndex) > length / 2 &&
            (index += index > curIndex ? -length : length); // always go in the shortest direction
          let newIndex = gsap.utils.wrap(0, length, index),
            time = times[newIndex];
          if (time > tl.time() || index > curIndex) {
            // if we're wrapping the timeline's playhead, make the proper adjustments
            vars.modifiers = { time: gsap.utils.wrap(0, tl.duration()) };
            time += tl.duration() * (index > curIndex ? 1 : -1);
          }
          curIndex = newIndex;
          vars.overwrite = true;
          return tl.tweenTo(time, vars);
        }
        tl.next = (vars) => toIndex(curIndex + 1, vars);
        tl.previous = (vars) => toIndex(curIndex - 1, vars);
        tl.current = () => curIndex;
        tl.toIndex = (index, vars) => toIndex(index, vars);
        tl.updateIndex = () =>
          (curIndex = Math.round(tl.progress() * (items.length - 1)));
        tl.times = times;
        tl.progress(1, true).progress(0, true); // pre-render for performance
        if (config.reversed) {
          tl.vars.onReverseComplete();
          tl.reverse();
        }
        if (config.draggable && typeof Draggable === "function") {
          let proxy = document.createElement("div"),
            wrap = gsap.utils.wrap(0, 1),
            ratio,
            startProgress,
            draggable,
            dragSnap,
            roundFactor,
            align = () =>
              tl.progress(
                wrap(startProgress + (draggable.startX - draggable.x) * ratio)
              ),
            syncIndex = () => tl.updateIndex();
          draggable = Draggable.create(proxy, {
            trigger: items[0].parentNode,
            type: "x",
            onPress() {
              startProgress = tl.progress();
              tl.progress(0);
              populateWidths();
              totalWidth = getTotalWidth();
              ratio = 1 / totalWidth;
              dragSnap = totalWidth / items.length;
              roundFactor = Math.pow(
                10,
                ((dragSnap + "").split(".")[1] || "").length
              );
              tl.progress(startProgress);
            },
            onDrag: align,
            onThrowUpdate: align,
            inertia: true,
            snap: (value) => {
              let n =
                Math.round(parseFloat(value) / dragSnap) *
                dragSnap *
                roundFactor;
              return (n - (n % 1)) / roundFactor;
            },
            onRelease: syncIndex,
            onThrowComplete: () => gsap.set(proxy, { x: 0 }) && syncIndex(),
          })[0];
        }
        return tl;
      }
    };
    const onLoad = () => {
      callback();
    };

    return (
      <div id="menuSlide" onLoad={onLoad}>
        {data.map((food, index) => (
          <div className="foods" key={index} id={`food${index}`}>
            <Link to={`/${storeId}/food/${food.name}`}> </Link>
            <svg
              viewBox="-50 0 430 510"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <ellipse
                cx="165"
                cy="251.5"
                rx="150"
                ry="237.5"
                fill={color[index]}
              />
              <path
                d="M327.5 251C327.5 319.894 309.138 382.151 279.584 427.111C250.015 472.091 209.451 499.5 165 499.5C120.549 499.5 79.985 472.091 50.4164 427.111C20.8616 382.151 2.5 319.894 2.5 251C2.5 182.106 20.8616 119.849 50.4164 74.8895C79.985 29.9095 120.549 2.5 165 2.5C209.451 2.5 250.015 29.9095 279.584 74.8895C309.138 119.849 327.5 182.106 327.5 251Z"
                stroke={color[index]}
                strokeWidth="5"
              />
            </svg>
            <span className="foodsname">
              {storeId === "korea" ? food.translate : food.name}
            </span>
            {/* <h3>{food.recommend === "yes" ? "Top 3 menu" : ""}</h3> */}
            <img
              src={food.image}
              alt="foodpic"
              className={menuId === "main" ? "imge" : "imge __drink"}
            />
            <div className="orderMsg">
              {/* <Link to={`/${storeId}/food/${food.name}`}> </Link> */}
              <span>click to order</span>
              <div>
                <span>&#9759;</span>
              </div>
            </div>
          </div>
        ))}
      </div>
    );
  }

  return (
    <div id="menuWrap">
      <FoodList />
    </div>
  );
}
